*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Noto Sans', sans-serif;
}

body{
  background-color: rgb(20, 20, 20);
}
.navbar{
    width: 100%;
    background: linear-gradient(to bottom, #000, transparent);
    padding: 15px 30px;
    position: fixed;
    display: flex;
    top: 0;
    z-index: 100;
}

.navbar > div:first-child{
    display: flex;
    align-items: center;
    width: 60%;
}

.colorMenu{
    background: linear-gradient(to bottom, #000, #161616);
}

.navbar-logo{
    width: 17%;
    margin-right: 30px;
}

.responsive-navbar{
    display: none;
}

.navbar-list{
    display: flex;
    list-style: none;
}

.navbar-list li{
    margin: 0 10px;
    font-size: 14px;
    cursor: pointer;
    transition: all .4s;
}

.link{
    text-decoration: none;
    color: #fff;
}

.navbar-list li:hover{
    color: rgb(168, 163, 163);
}

.navbar > div:nth-child(2){
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 40%;
    padding-right: 2vw;
}

.navbar .search-box{
    width: 230px;
    height: 30px;
    border: 1px solid #fff;
    background-color: #000;
    color: #fff;
    padding: 10px;
    font-size: 14px;
    margin-right: 10px;
    outline: none;
}

.navbar .navbar-icon{
    width: 30px;
    color: #fff;
    margin: 0 10px;
    font-size: 22px;
    cursor: pointer;
}

.navbar .navbar-kids{
    color: #fff;
    font-size: 15px;
    margin: 0 8px;
    cursor: pointer;
}

@media screen and (max-width: 1125px) {
    .navbar-list li{
        font-size: 12px;
        margin: 0 8px;
    }

    .navbar .search-box{
        width: 195px;
        height: 26px;
        padding: 8px;
        font-size: 12px;
        margin-right: 8px;
    }    

    .navbar .search-icon{
        font-size: 18px;
    }
    
    .navbar .navbar-icon{
        width: 26px;
        margin: 0 9px;
        font-size: 20px;
    }
    
    .navbar .navbar-kids{
        font-size: 15px;
    }    
}

@media screen and (max-width:875px){
    .navbar{
