    }
    
    .navbar > div:first-child{
        width: 50%;
    }
    
    .navbar > div:nth-child(2){
        width: 50%;
    }

    .navbar-logo{
        width: 20%;
        margin-right: 20px;
    }        
    
    .navbar-list{
        display: none;
    }

    .responsive-navbar{
        display: flex;
        flex-direction: column;
        color: #fff;
        padding: 20px 0;
    }

    .responsive-list{
        display: none;
        position: absolute;
        flex-direction: column;
        list-style: none;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.9);
        width: 270px;
        margin-left: -100px;
        margin-top: 35px;
        border-top: 2px solid #fff;
