    .responsive-list li{
        padding: 15px 0;
        cursor: pointer;
    }

    .responsive-list li:hover{
        background-color: rgba(219, 32, 44, .7);
    }

    .responsive-navbar:hover .responsive-list{
        display: flex;
    }

    .browse{
        font-size: 13px;
        cursor: default;
    }

    .sort-down-icon{
        margin: 0 5px;
    }

    .navbar .navbar-icon{
        width: 20px;
        margin: 0 9px;
        font-size: 16px;
    }
    
    .navbar .navbar-kids{
        font-size: 13px;
    }    
