/* --------------CAROUSEL---------------- */
.page-title {
  font-weight: bold;
  margin-left: 20px;
  font-size: 20px;
  margin-top: -25px;
  padding: 10px 0;
  color: #fff;
  -webkit-user-select: none;
      -ms-user-select: none;
