/* --------------TRENDING---------------- */
.trending-page{
  padding: 0 30px;
  margin-bottom: 40px;
}

.trending-title {
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 35px;
  font-weight: bold;
  margin-bottom: 20px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.single-component {
  display: flex;
  justify-content: center;
  transition: all 0.3s;
}

.single-component img {
  border-radius: 10px;
}

.single-component:hover {
  transform: scale(1.2);
  z-index: 1;
}

.single-component:hover > .info {
  display: flex;
}

.info {
  display: none;
  position: absolute;
  flex-direction: column;
  width: 97%;
  margin-top: 135px;
  padding: 5px 10px 10px 10px;
  background-color: rgb(20, 20, 20);
  color: #fff;
  box-shadow: 0 5px 20px #000;
}

.info h4 {
  font-size: 17px;
}

.add-icon {
  cursor: pointer;
  font-size: 25px;
}

.info-top h3 {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 5px;
  border-radius: 5px;
}

.info-top {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px 10px 10px;
  align-items: center;
}

.green {
  color: green;
}

.orange {
  color: orange;
}

.info-bottom {
  display: flex;
  font-size: 12px;
  padding-top: 10px;
}

.info-bottom span {
  padding-right: 10px;
  color: rgb(126, 124, 124);
}

/* --------------SEARCH---------------- */
.search{
  min-height: calc(100vh - 290px);
}

.form-searching-text {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 100px;
  margin-bottom: -50px;
}

.searching-text {
  outline: none;
  padding: 12px 10px;
  width: 350px;
  background-color: #000;
  border: 1px solid #fff;
  color: #fff;
}

.single-component-poster img {
  height: 370px;
}

.single-component:hover > .info-poster {
  display: flex;
}

.info-poster {
  display: none;
  position: absolute;
  flex-direction: column;
  width: 97%;
  bottom: 0;
  font-size: 10px;
  padding: 5px 10px 10px 10px;
  background-color: rgb(20, 20, 20);
  color: #fff;
  box-shadow: 0 5px 20px #000;
}

.form-searching-text button {
  background-color: transparent;
  border: none;
}

.searching-icon {
  font-size: 23px;
  margin: 0 10px;
  cursor: pointer;
}

@media screen and (max-width:1400px) {
