    font-weight: bold;
    text-shadow: 0 0 20px #000;
    background: rgba(0, 0, 0, 0.5);
    padding: 0 2px;
    border-radius: 3px;
}

.popular-title{
    text-align: start;
    margin-top: 5px;
    padding-right: 10px;
    padding-bottom: 10px;
    margin-left: -10px;
}

.info-bottom{
    margin-top: -20px;
}

.add-icon-text{
    display: none;
    position: absolute;
    margin-left: 25px;
    margin-top: -28px;
    padding: 5px 10px;
    font-size: 10px;
    background-color: rgba(0, 0, 0, 0.5);
}

.add-icon:hover > .add-icon-text{
    display: block;
}

.footer{
    width: 70%;
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-bottom: 20px;
}

.footer-icon{
    color: rgb(148, 142, 142);
    font-size: 30px;
    margin: 0 10px;
    cursor: pointer;
}

.footer-lists{
    display: flex;
    justify-content: space-between;
}

.footer-lists ul{
    list-style: none;
    padding-right: 20px;
    padding-top: 10px;
    font-size: 14px;
}

.footer-lists ul li{
    padding: 7px 0;
    color: rgb(148, 142, 142);
}

.footer-lists ul li:hover{
    text-decoration: underline;
    cursor: pointer;
}

.responsive-footer-lists{
    display: none;
    justify-content: space-between;
}

.responsive-footer-lists ul{
    list-style: none;
    padding-top: 10px;
    font-size: 14px;
}

.responsive-footer-lists ul li{
    padding: 7px 20px 7px 0;
    color: rgb(148, 142, 142);
}

.responsive-footer-lists ul li:hover{
    text-decoration: underline;
    cursor: pointer;
}


@media screen and (max-width:1000px) {
    .footer-lists ul li{
        font-size: 12px;
    }
}

@media screen and (max-width:800px) {
    .footer-lists{
        display: none;
    }

    .responsive-footer-lists{
        display: flex;
    }

    .responsive-footer-lists ul li{
        font-size: 11px;
    }
}

@media screen and (max-width:390px) {
    .responsive-footer-lists ul li{
        font-size: 9px;
    }
}
